				<b>
					<span style="  color:rgb(0,0,0);font-weight:bold;font-size:15.0000pt; "><font>管家解答：</font></span>
				</b>
				<b><span style="  color:rgb(0,0,0);font-weight:bold;font-size:15.0000pt; "></span></b>
			</p>
			<p style=" ">
				<span style="  color:rgb(255,0,0);font-size:12.0000pt; ">
					<font>一、问：</font>
					<font>X自由空间是怎么样做到安全和隐秘的。</font>
				</span>
				<span style="  color:rgb(255,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" ">
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "><font>答：</font></span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" ">
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "><font>1、</font></span>

				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "><font>进入空间无需个人身份授权，不会泄露个人信息。</font></span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style="mso-para-margin-left:0.0000gd; ">
				<span
					style="  font-size:12.0000pt;
		 "
				>
					<font>2、</font>
				</span>

				<span
					style="  font-size:12.0000pt;
		 "
				>
					<font>空间内的用户都是</font>
				</span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "><font>以虚拟的房间号进行交流和操作。</font></span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" ">
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "><font>3、</font></span>

				<span style="  color:rgb(0,0,0);font-size:12.0000pt; ">
					<font>房间如果设置密码，进入房间或观看动态需凭密码，如不设密码则动态对所有人开放。</font>
				</span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" ">
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "><font>4、</font></span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; ">
					<font>空间内的聊天记录可以一键删除，删除聊天双方手机上的聊天记录。</font>
				</span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" ">
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "><font>5、</font></span>

				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "><font>X自由空间是一款隐藏在微信里的小程序，一搜就有，一拉就没。</font></span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" "><span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span></p>
			<p style=" "><span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span></p>
			<p style=" ">
				<span style="  color:rgb(255,0,0);font-size:12.0000pt; "><font>二、问：怎么找到好友。</font></span>
				<span style="  color:rgb(255,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" ">
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; ">
					<font>答：如果好友已经在空间入住了，直接在房间内通过</font>
					<font>
						“搜索”好友的房间号或者个性签名关键字就可以找到好友；如果好友还未入住，可以通过房间内“邀请好友”，分享二维码给好友，等好友入住后，就可以在私聊里建立联系了。
					</font>
				</span>
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" "><span style="  color:rgb(0,0,0);font-size:12.0000pt; "></span></p>
			<p style=" ">
				<span style="  color:rgb(255,0,0);font-size:12.0000pt; "><font>三、问：房间里的鲜花、银子、便便的玩法。</font></span>
				<span style="  color:rgb(255,0,0);font-size:12.0000pt; "></span>
			</p>
			<p style=" ">
				<span style="  color:rgb(0,0,0);font-size:12.0000pt; ">
					<font>答：鲜花、便便、银子都可以赠送空间好友。如果你喜欢或者对某个房主感兴趣，都可以赠送银子或鲜花给</font>
